footer{
    margin-top: auto;
    border-top:1px solid;
    min-width:200px;
    text-align: center;
}
.centerFooter{
    min-width:80px;
    margin-left:auto;
    margin-right:auto;
    text-align: center;
}
body{
    background-color: lightslategray;
}
table{
    background-color: aqua;
    border: 1px solid;
    border-collapse: collapse;
}
td{
    border: 1px solid;
}
input{
    width:50px;
    height:50px;
}
div{
    margin:auto;
    width: 100%;
    display:flex;
    justify-content: center;
    overflow: auto; /* will contain if #first is longer than #second */
}
.subDiv{
    margin:auto;
    width: 100%;
    display:flex;
    justify-content: center;
    align-items:center;
    overflow: auto; /* will contain if #first is longer than #second */
    background-color: blue;
    flex-grow:1;

}
.blue{
    background-color: blue;
}
.red{
    background-color: red;
}
#wrapper{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.bm-burger-button {
    position: fixed;/*makes it stay when you scroll*/
    width: 36px;
    height: 30px;
    left: 0px;
    top: 0px;
    background-color: blue;
}
.bm-menu {
    position:absolute;
    background: gray;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
    width:300px;
}
.blue{
    background-color: blue;
}
.green{
    background-color: green;
}
.red{
    background-color: red;
}
.menuDiv{
    position:absolute;
    left:-650px;
    top:30px;
}
.bm-overlay {
    background: rgba(0, 0, 0, 0.9);
}
.bm-item-list {
    color: yellow;
    padding: 0.8em;
}
.bm-burger-bars {
    background: gray;
    width:25px;
    margin-left:auto;
    margin-right:auto;
  }